.puppyfw-field {
	padding-top: 1em;
	padding-bottom: 1em;
	border-bottom: 1px solid #f5f5f5;

	&:last-of-type {
		border-bottom: 0; }

	label[for] {
		display: block;
		font-weight: bold; } }

.puppyfw-field-textarea {
	textarea {
		width: 400px;
		height: 150px;
		max-width: 100%; } }

.puppyfw-map {
	max-width: 600px;
	height: 400px; }
