.puppyfw-tab {
	&__titles {
		list-style: none;
		margin: 0;
		padding: 0 0 0 pp-size(10, 13);
		position: relative;

		&:before {
			content: " ";
			display: block;
			height: 1px;
			width: 100%;
			background-color: $color-border;
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 1; }

		@extend %clear;

		li {
			margin: 0 pp-size(5, 13) 0 0;
			float: left;
			position: relative;
			z-index: 10;

			a {
				display: block;
				border: 1px solid $color-border;
				padding: 0.5em 1.5em;
				text-decoration: none;
				background-color: #f5f5f5;
				color: #777; }

			&.active a {
				border-bottom-color: #fff;
				background-color: #fff;
				color: #000; } } } }
